@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

body {
	background-color: whitesmoke;
	color: white;
	font-family: "Roboto", sans-serif;
	display: flex;
	/* align-items: center; */
	justify-content: center;
	min-height: 100vh;
	padding: 2rem;
}

h1 {
	margin: 1rem;
	text-align: center;
	color: orangered;
}


.winner-container {
	background: linear-gradient(to right, #fff0f6, #ffdeeb);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	grid-gap: 2rem;
	gap: 2rem;
	padding: 5rem;
	border-radius: 2rem;
	color: red;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

h3,
h2 {
	color: #d6336c;
	font-weight: bold;
	font-size: 2rem;
}
.winner-h {
	-webkit-transform: scale(2);
	        transform: scale(2);
	color: #74b816;
}

.btn-play {
	padding: 1rem 5rem;
	border: 2px solid #d6336c;
	border-radius: 10px;
	font-size: 1.5rem;
	font-weight: bold;
	background: #d6336c;

	color: white;
}

.btn-play:hover {
	border: 2px solid #d6336c;
	background: #e64980;
	color: #fff;
}

.squares {
	background: #f7f7f7;
	border: none;
	font-size: 5rem;
	/* font-weight: 800; */
	cursor: pointer;
	outline: none;
}

.X {
	color: red;
}
.O {
	color: green;
}

.board {
	background: #000;
	width: 450px;
	height: 450px;
	display: grid;
	grid-template: repeat(3, 1fr) / repeat(3, 1fr);
	grid-gap: 1rem;
	gap: 1rem;
	margin-top: 3rem;
}

.backdrop {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	z-index: 10;
	background: rgba(243, 157, 157, 0.75);
}

.modal {
	position: fixed;
	top: 30vh;
	left: 10%;
	width: 80%;
	z-index: 100;
	overflow: hidden;
}

.header {
	background: #d6336c;
	padding: 1rem;
}

.header h2 {
	margin: 0;
	color: white;
}

.content {
	padding: 1rem;
}

.content p {
	color: #000;
}

.actions {
	padding: 1rem;
	display: flex;
	justify-content: flex-end;
}

@media (min-width: 768px) {
	.modal {
		left: calc(50% - 20rem);
		width: 40rem;
	}
}

